import {reactive, computed} from 'vue';
import {createCounter} from "../../utils/createCounter";
import {MicroApp, MicroAppConfig, MicroAppLoader} from "../../../pro/micro.app";
import {$$notice} from "../notice-service";
import importHTML from 'import-html-entry';
import {defer} from "../../utils/defer";

const nextStackId = createCounter('navigator_stack')
const nextPageId = createCounter('navigator_page')

/*路由模式*/
export enum NavRouteMode {
    hash = 'hash',
    history = 'history',
}

/*生成stack的唯一标识的id*/
type GenerateStackId = (pageConfig: PageConfig) => string
/*获取页面组件对象*/
export type GetPage = (pageConfig: PageConfig) => Promise<any>
/*打开页面的时候的标题*/
type PageConfigTitle = string | ((pageConfig: PageConfig) => string)

/*跳转页面信息*/
export interface PageConfig<DATA = any> {
    title: PageConfigTitle,                                     // 页面标题
    path: string,                                               // 第一个页面的路径
    icon?: string,                                              // 图标
    param?: string,                                             // 页面参数（动态路由参数也是放这里边的）
    query?: Record<string, any>,                                // 路由的query参数
    data?: DATA,                                                 // 额外的数据
}

/*Page在Stack中具体信息*/
export interface Page<DATA = any> {
    id: string,                                                 // 页面标识
    pageConfig: PageConfig<DATA>,                                     // 页面信息
    init: boolean,                                              // 页面是否已经初始化
    loading: boolean,                                           // 页面是否处于加载状态
}

/*页面栈信息*/
export interface Stack<DATA = any> {
    id: string,                                                 // 页面栈id
    pageConfig: PageConfig<DATA>,                                     // 页面栈初始化的page
    pages: Page[],                                              // 页面栈数组
    show: boolean,                                              // 页面栈是否显示
}

/*创建NavigatorManager的配置参数*/
interface NavigatorManagerConfig {
    routerMode: NavRouteMode,                                   // 路由模式，解析url的时候是哈希路由还是history路由
    defaultPage: PageConfig,                                    // 当没有指定路由，也没有缓存的页面时，默认打开的页面
    storageKey?: string,                                        // 多页面应用中可能会存在缓存冲突的问题。通过这个属性可以隔离多页面应用之间的缓存
    maxStack?: number,                                          // 最大的可以打开的stack个数
    generateStackId?: GenerateStackId,                          // 生成StackId的函数
    microAppConfig?: MicroAppConfig[],
}

export function createNavigatorManager(config: NavigatorManagerConfig) {

    const apps: MicroApp[] = (config.microAppConfig || []).map(config => ({config}))
    const state = reactive({stacks: [] as Stack[],})
    /*当前正在显示的tab*/
    const currentStack = computed(() => state.stacks.filter(s => s.show).shift())

    /*---------------------------------------utils-------------------------------------------*/

    const utils = {
        /*创建Page*/
        createPage: (pageConfig: PageConfig): Page => {
            return {
                id: nextPageId(),
                pageConfig,
                init: false,
                loading: false,
            }
        },
        /*创建Stack*/
        createStack: (pageConfig: PageConfig): Stack => {
            return {
                id: (config.generateStackId || nextStackId)(pageConfig),
                pageConfig: pageConfig,
                pages: [utils.createPage(pageConfig),],
                show: false,
            }
        },
        /*获取页面加载器*/
        getAppLoader: async (pageConfig: PageConfig): Promise<MicroAppLoader | undefined> => {
            const app = apps.filter(a => a.config.pattern.test(pageConfig.path)).shift()
            if (!app) {
                console.error('无子应用可以处理该页面！', pageConfig)
                return
            }
            console.log('匹配子应用：', {app: app.config.name, url: pageConfig.path})
            if (!!app.config.loader) {return app.config.loader}
            if (!app.config.url) {
                console.error('子应用信息错误，app.config.url 以及 app.config.loader 不能同时为空')
                return
            }
            if (!app.loader) {
                try {
                    if (!!app.loadWork) {
                        await app.loadWork
                    } else {
                        console.log('加载子应用', app.config.name)
                        const dfd = defer<MicroAppLoader>()
                        app.loadWork = dfd.promise
                        const html = await importHTML(app.config.url!)

                        const styles: string[] = await html.getExternalStyleSheets()
                        styles.forEach(style => {
                            const ele = document.createElement('style') as HTMLStyleElement
                            ele.innerHTML = style
                            document.body.appendChild(ele)
                        })

                        app.assetPublicPath = html.assetPublicPath
                        const bootstrap = ((await html.execScripts()) as any).default
                        app.loader = await bootstrap(app)
                        dfd.resolve(app.loader!);
                    }
                } catch (e) {
                    $$notice.error(`加载子应用【${app.config.name}】失败！`)
                    throw  e
                }
            }
            return app.loader!
        },
        /*查找一个Stack*/
        findStack<DATA = any>(judgement: (stack: Stack<DATA>) => boolean): Stack<DATA> | undefined {
            return state.stacks.find(judgement)
        },
        registryMicroApp: (config: MicroAppConfig) => {
            apps.unshift({config})
        },
    }
    const tabMethods = {
        /*打开一个tab*/
        openTab: async (pageConfig: PageConfig) => {
            const newStack = utils.createStack(pageConfig)
            state.stacks.push(newStack)
            await tabMethods.showTab(newStack.id)
        },
        /*关闭tab*/
        closeTab: () => {/*todo*/},
        /*刷新tab*/
        refreshTab: () => {/*todo*/},
        /*获取当前tab*/
        getCurrentTab: () => {/*todo*/},
        /*显示tab*/
        showTab: async (stackId: string) => {
            await tabMethods.hideTab()
            const stack = state.stacks.find(item => item.id === stackId)
            if (!!stack) {
                stack.show = true
                /*渲染最后两个页面*/
                if (stack.pages.length > 0 && !stack.pages[stack.pages.length - 1].init) {
                    stack.pages[stack.pages.length - 1].init = true
                }
                if (stack.pages.length > 1 && !stack.pages[stack.pages.length - 2].init) {
                    stack.pages[stack.pages.length - 2].init = true
                }
            }
        },
        /*隐藏当前显示的tab*/
        hideTab: async () => {
            const current = currentStack.value
            if (!!current) {
                current.show = false
            }
        },
    }
    const pageMethods = {
        /*push一个页面*/
        push: (stack: Stack, pageConfig: PageConfig) => {
            stack.pages.push(utils.createPage(pageConfig))
        },
        /*pop一个页面*/
        back: (stack: Stack) => {
            stack.pages.length > 1 && stack.pages.pop()
        },
        /*重定向到一个页面*/
        redirect: () => {/*todo*/},
        /*刷新页面*/
        refresh: () => {/*todo*/},
        /*开启/关闭页面的加载状态*/
        loading: () => {/*todo*/},
        /*获取当前页面信息*/
        getCurrentPage: () => {/*todo*/},
        /*监听事件*/
        on: () => {/*todo*/},
        /*解除监听事件*/
        off: () => {/*todo*/},
        /*监听一次事件*/
        once: () => {/*todo*/},
        /*派发事件*/
        emit: () => {/*todo*/},
    }

    /*执行初始化逻辑*/
    const init = async () => {
        /*根据路由地址初始化*/
        /*根据缓存初始化*/
        /*根据defaultPage初始化*/
        await tabMethods.openTab(config.defaultPage)
    }

    return reactive({
        state,
        currentStack,
        utils,
        tabMethods,
        pageMethods,
        ...tabMethods,
        registryMicroApp: utils.registryMicroApp,
        init,
    })
}

export type NavigatorManager = ReturnType<typeof createNavigatorManager>